  opacity: 0;
  transition: all .3s;
  position: absolute;
  inset: 0;
  overflow: auto;
  transform: translateX(-100%);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

